<template>
<!--自定义指令值的写法-->
  <!--<div v-theme="'narrow'" id="show-blogs">-->
  <!--添加参数column-->
  <div v-theme:column="'narrow'" id="show-blogs">
    <h1>博客总览</h1>
    <input type="text" v-model="search" placeholder="搜索">
    <!--<div v-for="blog in blogs" class="single-blog">-->
    <div v-for="blog in filteredBlogs" class="single-blog">
        <!--调用自定义指令和过滤器(to-语法)--><!--dom中显示的是a标签，添加v-bind是用于传值到SingleBlog-->
        <router-link v-bind:to="'/blog/'+blog.id"><h2 v-rainbow>{{blog.id}}.{{blog.title | to-uppercase}}</h2></router-link>
        <article><!--snippet全局过滤器-->
            {{blog.content | snippet}}
        </article>
    </div>
  </div>
</template>

<script>

export default {
  name: 'show-blogs',
  data(){
      return{
          blogs:[],
          //过滤信息
          search:"",
      }
  },
  created(){
        //请求网络资源
        /*
        this.$http.get("https://jsonplaceholder.typicode.com/posts").then(function(data){  
        //console.log(data);
        this.blogs = data.body.slice(0,10);//return 10 data
        //console.log(this.blogs);
        })
        */
        //请求本地资源
        //this.$http.get("./../static/posts.json").then(function(data){  
        //console.log(data);
        //this.blogs = data.body.slice(0,10);//return 10 data
        //})
        this.$http.get("https://vuedemo-280a1.firebaseio.com/posts.json").then(function(data){  
            console.log(data.json());
            return data.json();
        }).then(function(data){
            var blogsArray = [];
            for(let key in data){
                console.log(key);
                console.log(data[key]);
                data[key].id = key;
                blogsArray.push(data[key]);
            }
            console.log(blogsArray);
            this.blogs = blogsArray;
            console.log("-----------------------");
            console.log(this.blogs);
        })
  },
  //无需跳转刷新页面
  computed:{
      //过滤blogs数据，匹配搜索,更改blogs
      filteredBlogs: function(){
          return this.blogs.filter((blog)=>{
              return blog.title.match(this.search);//search内容匹配blog.title
          })
      }
  },
  //自定义局部过滤器
  filters:{
      /*方法一
      "to-uppercase":function(value){
        return value.toUpperCase();
       */
      //方法二ES6语法,toUppercase为to-uppercase
      toUppercase(value){
        return value.toUpperCase();
      },
  },
  //自定义局部指令
  directives:{
      'rainbow':{
          bind(el,binding,vnode){
              el.style.color = "red";
          }
      }
  }
}
</script>

<style>
#show-blogs {
    max-width: 800px;
    margin: 0 auto;
}

.single-blog {
    padding: 20px;
    margin: 20px 0;
    box-sizing: border-box;
    background: #eee;
    border: 1px dotted #aaa;
}

#show-blogs a{
    color: #444;
    text-decoration: none;
}

input [type="text"]{
    padding: 8px;
    width: 100%;
    box-sizing: border-box;
}
</style>


