<template>
  <div id="add-blog">
    <h2>添加博客</h2>
    <form action="" v-if="!submmited">
        <label for="">博客标题</label>
        <input type="text" v-model="blog.title" required />
        <label for="">博客内容</label>
        <textarea name="" id="" cols="30" rows="10" v-model="blog.content"></textarea>
        <div id="checkboxes">
            <label for="">Vue.js</label>
            <input type="checkbox" value="Vue.js" v-model="blog.categories">
            <label for="">Node.js</label>
            <input type="checkbox" value="Node.js" v-model="blog.categories">
            <label for="">React.js</label>
            <input type="checkbox" value="React.js" v-model="blog.categories">
            <label for="">Angular4.js</label>
            <input type="checkbox" value="Angular4.js" v-model="blog.categories">
        </div>
        <label for="">作者</label>
        <select name="" id="" v-model="blog.author">
            <option v-for="author in authors">{{author}}</option>
        </select>
        <!--prevent防止刷新页面-->
        <button v-on:click.prevent="post">添加博客</button>
    </form>

    <div v-if="submmited">
        <h3>您的博客发布成功</h3>
    </div>

    <div id="preview">
        <h3>博客总览</h3>
        <p>博客标题：{{blog.title}}</p>
        <p>博客内容：</p>
        <p>{{blog.content}}</p>
        <p>博客分类</p>
        <ul>
            <li v-for="cat in blog.categories">{{cat}}</li>
        </ul>
        <p>作者：{{blog.author}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'add-blog',
  data () {
    return {
      blog:{
          title: "",
          content: "",
          categories:[],
          author: ""
      },
      authors:["张三","李四","王五"],
      submmited:false,
    }
  },
  methods:{
      post:function(){
          /*
          this.$http.post("http://jsonplaceholder.typicode.com/posts",{
              title:this.blog.title,
              body:this.blog.content,
              userId:1
          }).then(function(data){
              console.log(data);
              this.submmited = true;
          });//post成功后执行then
          */
         //上传数据库,posts.json自动创建
         this.$http.post("https://vuedemo-280a1.firebaseio.com/posts.json",
              //可传递数组{},
              //或者直接传递对象
              this.blog
          ).then(function(data){
              console.log(data);
              this.submmited = true;
          });
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#add-blog *{
    box-sizing: border-box;/*border-box 设置的边框和内边距的值是包含在width内的*/
}

#add-blog {
    margin: 20px auto;
    max-width: 600px;
    padding: 20px;
}

label {
    display:block;/*独占一行*/
    margin: 20px 0 10px;
}

input[type="text"], textarea, select {
    display:block;
    width: 100%;
    padding: 8px;
}

textarea {
    height: 200px;
}

#checkboxes label {
    display: inline-block;/*一行*/
    margin-top: 0;
}

#checkboxes input {
    display: inline-block;
    margin-right: 10px;
}

button {
    display: block;
    margin: 20px 0;
    background: crimson;
    color: #fff;
    border: 0;
    padding: 14px;
    border-radius: 4px;
    font-size: 18px;
    cursor: pointer;/*光标形状*/
}

#preview {
    padding: 10px 20px;
    border: 1px dotted #ccc;
    margin: 30px 0;
}

h3 {
    margin-top: 10px;
}
</style>



